<template>
  <div class="logo">
    <router-link :to="`${prevPath}/dw/survey`" >
      <!--              <img class="dw-header-logo-img" alt="element-logo"  src="data:image/svg+xml;base64," >-->
      <el-image :src="require('@/assets/logo.png')" ></el-image>
      <span style="color: #ffa71d;font-size: 14px;position: absolute;margin-left: 10px;">Pro</span>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'DwHeaderLogo',
  data () {
    return {
      userName: 'dwsurvey',
      prevPath: '/v6'
    }
  },
  mounted () {
    const routePath = this.$route.path
    if (routePath.indexOf('/v6/lr') >= 0) {
      this.prevPath = '/v6/lr'
    }
  }
}
</script>

<style scoped>

</style>



